import E from 'wangeditor'
import { setTimeout } from 'timers';
export default {
  name: 'Editorbar',
  data () {
    return {
      editor: null,
      info_: null
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    isClear: {
      type: Boolean,
      default: false
    },
  },
  watch: {
    isClear (val) {
      // 
      if (val) {
        this.editor.txt.clear()
        this.info_ = null
      }
    }
  },
  mounted () {
    this.seteditor()
    setTimeout(()=>{
        this.editor.txt.html(this.value)
    },1000)
  },
  methods: {
    seteditor () {
      this.editor = new E(this.$refs.toolbar, this.$refs.editor)
      this.editor.customConfig.uploadImgShowBase64 = true // base 64 
      this.editor.customConfig.uploadImgServer = this.GLOBAL.BASE_URL+'/api/Administration/group/UploadImage1'

      // 
      this.editor.customConfig.menus = [
        'head', // 
        'bold', // 
        'fontSize', // 
        'fontName', // 
        'italic', // 
        'underline', // 
        'strikeThrough', // 
        'foreColor', // 
        'backColor', // 
        'link', // 
        'list', // 
        'justify', // 
        'quote', // 
        'emoticon', // 
        'image', // 
        'table', // 
        'video', // 
        'code', // 
        'undo', // 
        'redo' // 
      ]

      this.editor.customConfig.uploadImgHooks = {
        fail: (xhr, editor, result) => {
          // 
        },
        success: (xhr, editor, result) => {
          // 
        },
        timeout: (xhr, editor) => {
          // 
        },
        error: (xhr, editor) => {
          // 
        },
        customInsert: (insertImg, result, editor) => {
          // ,
          insertImg(result.data[0])
        }
      }
      this.editor.customConfig.onchange = (html) => {
        this.info_ = html // 
        this.$emit('change', this.info_) // 
      }

      // 
      this.editor.create()     // 
    }
  }
}



